//
import '../css/sy.css'
import { Input, Select,Button } from 'antd';
import Sy_3_1 from './sy_3_1'
import Sy_3_2 from './sy_3_2'
import Sy_3_biao1 from '../../components/sy_3_biao1'
import { Route, Switch } from 'react-router';
import { Link } from 'react-router-dom';

const { Option } = Select;

function Sy_3() {
   
    return(
        <div>
            <div className="sy-1-input">
                <Input.Group compact>
                    <Select defaultValue="Option9" style={{width:200,height:34}}>
                        <Option value="Option1"></Option>
                        <Option value="Option2"></Option>
                        <Option value="Option3"></Option>
                        <Option value="Option4"></Option>
                        <Option value="Option5"></Option>
                        <Option value="Option6"></Option>
                        <Option value="Option7"></Option>
                        <Option value="Option8"></Option>
                        <Option value="Option9"></Option>
                        <Option value="Option10"></Option>
                        <Option value="Option11"></Option>
                    </Select>
                    <Select defaultValue="Option1" style={{width:120,height:34,marginLeft:20}}>
                        <Option value="Option1"></Option>
                        <Option value="Option2"></Option>
                    </Select>
                    <Select defaultValue="" style={{width:120,height:34,marginLeft:20}}  >
                        <Option value="Option1"></Option>
                        <Option value="Option2"></Option>
                    </Select>
                    <Select defaultValue="" style={{width:120,height:34,marginLeft:20}}>
                        <Option value="Option1" ><Link to="/App/box/sy/sy_3/" style={{color:'black'}}> </Link></Option>
                        <Option value="Option2"><Link to="/App/box/sy/sy_3/sy_3_2" style={{color:'black'}}> </Link></Option>
                    </Select>
                    <Button type="primary" style={{marginLeft:20,marginTop:'auto'}}></Button>
                </Input.Group>
            </div>
            <div className="sy-1-tong" style={{marginBottom:30}}>
                <Switch>
                    <Route path="/App/box/sy/sy_3/" component={Sy_3_1} exact />
                    <Route path="/App/box/sy/sy_3/sy_3_2" component={Sy_3_2}  />
                </Switch>
            </div>
            <div className="sy-1-biao">
                    <Sy_3_biao1></Sy_3_biao1>
            </div>
        </div>
    )
}

export default Sy_3